          .weixin{\n                    margin-left: 20px;\n                    height: 32px;\n                    width: 32px;\n                }\n                .wxCode{\n                    position: absolute;\n                    height: 120px;\n                    width: 120px;\n                    background: #0abf9b;\n                    text-align: center;\n                    top: -25px;\n                    left: 70px;\n                    .wximg{\n                        height: 100px;\n                        width: 100px;\n                        margin: 10px auto;\n                    }\n                    .triangle-left {\n                        position: absolute;\n                        display: inline-block;\n                        width: 0;\n                        height: 0;\n                        border-top: 8px solid transparent;\n                        border-right: 10px solid #0abf9b;\n                        border-bottom: 8px solid transparent;\n                        left: -10px;\n                        top: 30px;\n                    }\n                }\n            }\n        }\n    }\n    .aboutContent{\n        background: #0abf9b;\n        color: #fff;\n        width: 300px;\n        height: 350px;\n        padding: 20px 15px;\n        margin-left: 20px;\n        .aboutTitle{\n            font-size: 26px;\n        }\n        .aboutContents{\n            display: flex;\n            padding-top:20px;\n            cursor: pointer;\n            .backicon{\n                height: 60px;\n                width: 104px;\n                background-size:cover;\n            }\n            .content{\n                display: inline-block;\n                padding-left: 10px;\n                width:130px;\n            }\n        }\n    }\n\n}\n.moreNews{\n    width: 100%;\n    padding: 30px 10% 30px;\n    .moreNewstitle{\n        font-size: 24px;\n        padding-bottom: 15px;\n        border-bottom: 3px solid #0abf9b;\n        font-weight: 600;\n    }\n    .healthnewsContent{\n        display:flex;\n        margin-top: 30px;\n        .newsLeft{\n            height: 200px;\n            width: 30%;\n            margin-right: 24px;\n            background-size: cover;\n            border: 1px solid #f2f2f2;\n            .lable{\n                padding: 0 8px 0;\n                height: 38px;\n                background-color: #0abf9b;\n                line-height: 38px;\n                text-align: center;\n                .lableIcon{\n                    img{\n                        height: 18px;\n                        width: 18px;\n                    }\n                }\n                .lableText{\n                    color: #fff;\n                    font-size: 12px;\n                    display: inline-block;\n                    vertical-align: middle;\n                }\n            }\n        }\n        .newsRight{\n            position: relative;\n            width: 55%;\n\n            .rightTop{\n                position: absolute;\n                top: 10px;\n                .topTitle{\n                    color: #3d3d3d;\n                    font-size: 18px;\n                }\n                .topText{\n                    font-size: 14px;\n                    color: #929292;\n                }\n            }\n            .newsTime{\n                position: absolute;\n                bottom: 10px;\n                color: #929292;\n                font-size: 12px;\n            }\n        }\n    }\n    .moreData{\n        cursor: pointer;\n        text-align: center;\n        height: 40px;\n        width: 100px;\n        line-height: 40px;\n        color: #0abf9b;\n        margin: 50px auto 10px;\n        border: 2px solid #0abf9b;\n    }\n}\n.codeBox{\n    right: 2%;\n    margin-top: 56px;\n    display: flex;\n    position: fixed;\n    bottom: 120px;\n    .emailImg{\n        height: 59px;\n        width: 200px;\n        background: #0abf9b;\n        color: #232323;\n        position: absolute;\n        left: -210px;\n        line-height: 59px;\n        font-size: 14px;\n        text-align: center;\n    }\n    .phoneImg{\n        height: 59px;\n        background: #0abf9b;\n        color: #232323;\n        width: 200px;\n        position: absolute;\n        left: -210px;\n        top: 68px;\n        line-height: 59px;\n        font-size: 14px;\n        text-align: center;\n    }\n    .emailInfo{\n        background: #0abf9b;\n        font-size: 12px;\n        color: #fff;\n        text-align: center;\n        cursor: pointer;\n        margin-bottom: 8px;\n        padding: 10px 17px;\n        img{\n            height: 20px;\n            width: 20px;\n            margin-bottom: 2px;\n        }\n        p{\n            margin-bottom: 0px;\n        }\n        .shangfan{\n            height: 12px;\n            width: 15px;\n            cursor: pointer;\n        }\n    }\n    .emailInfo:hover{\n        color: #0d0d0d;\n    }\n    .none{\n        display: none;\n    }\n    .active{\n        color: #0d0d0d;\n    }\n}\n@media screen and (min-width: 1920px) {\n    .moreNews {\n        .healthnewsContent {\n            .newsLeft{\n                height: 420px;\n                background-size: cover;\n                width:35%;\n            }\n        }\n    }\n}\n@media screen and (max-width: 1330px) {\n    .moreNews{\n        width: 100%;\n        padding: 30px 10% 50px;\n        .moreNewstitle{\n            font-size: 18px;\n            padding-bottom: 15px;\n            border-bottom: 3px solid #0abf9b\n        }\n        .healthnewsContent{\n            display:flex;\n            margin-top: 30px;\n            cursor: pointer;\n            .newsLeft{\n                height: 200px;\n                width: 30%;\n                margin-right: 24px;\n                .lable{\n                    padding: 0 8px 0;\n                    height: 32px;\n                    background-color: #0abf9b;\n                    line-height: 32px;\n                    text-align: center;\n                    .lableIcon{\n                        img{\n                            height: 16px;\n                            width: 16px;\n                        }\n                    }\n                    .lableText{\n                        color: #fff;\n                        font-size: 12px;\n                        display: inline-block;\n                        vertical-align: middle;\n                    }\n                }\n            }\n            .newsRight{\n                position: relative;\n                width: 55%;\n                .rightTop{\n                    position: absolute;\n                    top: 0;\n                    .topTitle{\n                        color: #3d3d3d;\n                        font-size: 18px;\n                    }\n                    .topText{\n                        font-size: 14px;\n                        color: #929292;\n                    }\n                }\n                .newsTime{\n                    position: absolute;\n                    bottom: 0;\n                    color: #929292;\n                    font-size: 12px;\n                }\n            }\n        }\n        .moreData{\n            cursor: pointer;\n            text-align: center;\n            height: 40px;\n            width: 100px;\n            line-height: 40px;\n            color: #0abf9b;\n            margin: 50px auto 10px;\n            border: 2px solid #0abf9b;\n        }\n    }\n}\n/****************** pc specail end *********************************/\n\n/**************** mobile specail start ******************************/\n\n@media screen and (max-width: 768px) {\n\n    .moreNews{\n        width: 100%;\n        padding: 20px 10%;\n        .moreNewstitle{\n            font-size: 18px;\n            padding-bottom: 15px;\n            border-bottom: 3px solid #0abf9b\n        }\n        .healthnewsContent{\n            display:flex;\n            margin-top: 15px;\n            cursor: pointer;\n            .newsLeft{\n                height: 200px;\n                width: 30%;\n                margin-right: 24px;\n                .lable{\n                    padding: 0 8px 0;\n                    height: 32px;\n                    background-color: #0abf9b;\n                    line-height: 32px;\n                    text-align: center;\n                    .lableIcon{\n                        img{\n                            height: 16px;\n                            width: 16px;\n                        }\n                    }\n                    .lableText{\n                        color: #fff;\n                        font-size: 12px;\n                        display: inline-block;\n                        vertical-align: middle;\n                    }\n                }\n            }\n            .newsRightmobile{\n                .rightTop{\n                    .topTitle{\n                        color: #3d3d3d;\n                        font-weight: 600;\n                        font-size: 18px;\n                    }\n                    .topText{\n                        font-size: 14px;\n                        color: #929292;\n                    }\n                }\n                .newsTime{\n                    bottom: 5px;\n                    color: #929292;\n                    font-size: 12px;\n                }\n            }\n        }\n        .moreData{\n            cursor: pointer;\n            text-align: center;\n            height: 40px;\n            width: 100px;\n            line-height: 40px;\n            color: #0abf9b;\n            margin: 30px auto 10px;\n            border: 1px solid #0abf9b;\n\n        }\n    }\n}\n@media screen and (max-width: 767px) {\n    .newsDetail{\n        width: 100%;\n        padding: 40px 15px 40px;\n        display: block;\n        .newsContent{\n            width: 100%;\n            margin-right: 0;\n            .articleContent{\n                overflow: hidden!important;\n                table{\n                    width: 100%!important;\n                }\n                img{\n                    /*width: 100% !important;*/\n                }\n            }\n            .detailTitle{\n                font-size: 20px;\n                color:#0d0d0d;\n            }\n            .authInfo{\n                padding: 25px 0;\n                color: #929292;\n                font-size: 12px;\n                display: flex;\n                line-height: 32px;\n                .weixinCode{\n                    position: relative;\n                    .weixin{\n                        margin-left: 20px;\n                        height: 32px;\n                        width: 32px;\n                    }\n                    .wxCode{\n                        position: absolute;\n                        height: 100px;\n                        width: 100px;\n                        background: #0abf9b;\n                        text-align: center;\n                        top: -20px;\n                        left: 70px;\n                        .wximg{\n                            height: 80px;\n                            width: 80px;\n                            margin: 10px auto;\n                        }\n                        .triangle-left {\n                            position: absolute;\n                            display: inline-block;\n                            width: 0;\n                            height: 0;\n                            border-top: 8px solid transparent;\n                            border-right: 10px solid #0abf9b;\n                            border-bottom: 8px solid transparent;\n                            left: -10px;\n                            top: 30px;\n                        }\n                    }\n                }\n            }\n        }\n        .aboutContent{\n            background: #0abf9b;\n            color: #fff;\n            width: 100%;\n            height: 350px;\n            padding: 20px 15px;\n            margin-left: 0px;\n            margin-top: 20px;\n            .aboutTitle{\n                font-size: 28px;\n            }\n            .aboutContents{\n                display: flex;\n                padding-top:20px;\n                cursor: pointer;\n                .backicon{\n                    height: 60px;\n                    width: 104px;\n                    background-size:100% 100%;\n                }\n                /*img{*/\n                    /*height: 60px;*/\n                    /*width: 104px;*/\n                    /*display: inline-block;*/\n                /*}*/\n                .content{\n                    display: inline-block;\n                    padding-left: 10px;\n                    min-width: 200px;\n                }\n            }\n        }\n\n    }\n    .moreNews{\n        width: 100%;\n        padding: 20px 15px;\n        .moreNewstitle{\n            font-size: 16px;\n            padding-bottom: 10px;\n            border-bottom: 3px solid #0abf9b\n        }\n        .healthnewsContent{\n            cursor: pointer;\n            display:block;\n            margin-top: 30px;\n            .newsLeft{\n                height: 180px;\n                width: 100%;\n                margin-right: 15px;\n                .lable{\n                    height: 32px;\n                    padding: 0 8px 0;\n                    background-color: #0abf9b;\n                    line-height: 32px;\n                    text-align: center;\n                    .lableIcon{\n                        img{\n                            height: 16px;\n                            width: 16px;\n                        }\n                    }\n                    .lableText{\n                        color: #fff;\n                        font-size: 12px;\n                        display: inline-block;\n                        vertical-align: middle;\n                    }\n                }\n            }\n            .newsRightmobile{\n                .rightTop{\n                    padding-top: 15px;\n                    .lead{\n                        margin-bottom: 15px;\n                        font-weight: 600;\n                    }\n                    .topTitle{\n                        color: #3d3d3d;\n                        font-weight: 600;\n                        font-size: 18px;\n                    }\n                    .topText{\n                        font-size: 14px;\n                        color: #929292;\n                    }\n                }\n                .newsTime{\n                    bottom: 5px;\n                    color: #929292;\n                    font-size: 12px;\n                }\n            }\n        }\n    }\n}\n/**************** mobile specail end *****************************/\n"],"sourceRoot":""}]);

